<template>
  <div id="app">
    <div class="sidebar">
      <div class="header">
        <img @mouseover="rotateover" @mouseout="rotateout" :class="['animate__animated',rotate]" src="/1.jpg" alt="">
      </div>
      <div class="time"><span>{{gettime}}</span></div>
      <div class="progress">
        <h1>‰ªªÂä°ËøõÂ∫¶</h1>
        <p><span :style="{width:progress*100+'%'}">&nbsp;&nbsp;&nbsp;{{Math.floor(progress*100)}}%</span></p>
        <p>ËøòÂâ©{{this.todo_list.length}}‰∏™‰ªªÂä°Êú™ÂÆåÊàê</p>
      </div>
      <div class="progress">
        <h1>Day</h1>
        <p><span
            :style="{width:Math.floor(timing.getHours()/24*100)+'%'}">&nbsp;&nbsp;&nbsp;{{Math.floor(timing.getHours()/24*100)}}%</span>
        </p>
        <p>‰ªäÂ§©Ââ©‰∏ã{{24-timing.getHours()}}Â∞èÊó∂</p>
      </div>

    </div>
    <div class="box">
      <!-- Ê∑ªÂä†‰ªªÂä° -->
      <form class="add" @submit.prevent="add">
        <span>ToDoList</span>
        <input type="text" v-model="something" placeholder="Ê∑ªÂä†‰ªªÂä°">
        <button>üñïÁÇπÂáªÊ∑ªÂä†</button>
      </form>
      <!-- Êú™ÂÆåÊàê -->
      <div class="todo" @mousemove="mousemove('todo_list')">
        <div class="title" @click="clear_todo('todo_list')">ToDo({{todo_num}})<span>&nbsp;üëàÁÇπÊàëÊ∏ÖÈô§</span></div>
        <ul class="list">
          <li style="background-color: #f6f9fb;user-select: none;" v-if="!todo_list.length" @dragstart="drag($event,)"
            @drop="drop($event,'false','todo_list')" @dragover='allowDrop($event)'>‰Ω†ËøòÊ∑ªÂä†‰ªª‰ΩïÂæÖÂäû
          </li>
          <li class="item" v-for="(v,i) in todo_list" draggable="true" @dragstart="drag($event,i)"
            @drop="drop($event,i,'todo_list')" @dragover='allowDrop($event)'>
            <i class="iconfont icon-icon--" @click="toDone(i)"></i>
            <span :title="v.title">{{v.title}}</span>
            <i class="iconfont icon-shanchu" @click="remove('todo',i)"></i>
          </li>
        </ul>
      </div>
      <!-- Â∑≤ÂÆåÊàê -->
      <div class="done" @mousemove="mousemove('done_list')">
        <div class="title" @click="clear_todo('done_list')">Done({{done_num}})<span>&nbsp;üëàÁÇπÊàëÊ∏ÖÈô§</span></div>
        <ul class="list">
          <li style="background-color: #f6f9fb;user-select: none;" v-if="!done_list.length" @dragstart="drag($event)"
            @drop="drop($event,'false','done_list')" @dragover='allowDrop($event)'>‰Ω†ËøòÊú™ÂÆåÊàê‰ªª‰ΩïÂæÖÂäû</li>
          <li class="item" v-for="(v,i) in done_list" draggable="true" @dragstart="drag($event,i)"
            @drop="drop($event,i,'done_list')" @dragover='allowDrop($event)'>
            <i class="iconfont icon-shouye" @click="toDo(i)"></i>
            <span :title="v.title">{{v.title}}</span>
            <i class="iconfont icon-shanchu" @click="remove('done',i)"></i>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import 'animate.css'
export default {
  name: 'App',
  data () {
    return {
      something: "",
      todo_list: [],
      done_list: [],
      list_type: '',
      timer: null,
      timing: new Date(),
      rotate: ''
    }
  },
  watch: {
    todo_list () {
      localStorage.todo_list = JSON.stringify(this.todo_list)
    },
    done_list () {
      localStorage.done_list = JSON.stringify(this.done_list)
    }
  },
  computed: {
    todo_num () { return this.todo_list.length },
    done_num () { return this.done_list.length }, gettime: function () { //Êó∂Èó¥Ê†ºÂºèÂåñ
      var time = this.timing
      var arr1 = [time.getFullYear(), time.getMonth() + 1, time.getDate()]
      var arr2 = [time.getHours(), time.getMinutes(), time.getSeconds().toString().padStart(2, '0')]
      return arr1.join(', ') + ' - ' + arr2.join(':')
    },
    progress () {//ËøõÂ∫¶Êù°
      var todo = this.todo_list.length
      var done = this.done_list.length
      if (!done / (todo + done)) {
        return 0
      } else {
        return done / (todo + done)
      }

    }
  },
  methods: {
    add () {//Ê∑ªÂä†‰ªªÂä°
      if (!this.something) {
        alert('ÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫')
      } else {
        this.todo_list.push({ title: this.something, done: false })
        this.something = ''
      }
    },
    toDone (i) {//‰ªªÂä°ÂÆåÊàê‰∫ã‰ª∂
      this.todo_list[i].done = true
      this.done_list.push(this.todo_list[i])
      this.todo_list.splice(i, 1)
    },
    toDo (i) {//‰ªªÂä°ÂèñÊ∂àÂÆåÊàê‰∫ã‰ª∂
      this.done_list[i].done = false
      this.todo_list.push(this.done_list[i])
      this.done_list.splice(i, 1)
    },
    remove (type, i) {//Âà†Èô§‰ªªÂä°‰∫ã‰ª∂
      if (type == 'todo') {
        this.todo_list.splice(i, 1)
      } else if (type == 'done') {
        this.done_list.splice(i, 1)
      }
    },
    drag (event, index) {//ÂºÄÂßãÊãñÂä®
      event.dataTransfer.setData('index', index)//ËÆ∞ÂΩïÊãñÂä®ÂÖÉÁ¥†ÁöÑÂÖ≥ÈîÆÊï∞ÊçÆ
    },
    mousemove (e) {//‰ªéÂì™ÂÑøÊãñÂä®
      this.list_type = e
    },
    drop (event, index, type) {//ÊãñÂä®ÁöÑÂÖÉÁ¥†Ë¢´ÊîæÂà∞‰∫ÜÊüê‰∏ÄÂÖÉÁ¥†‰∏≠
      event.preventDefault();
      let startIndex = parseInt(event.dataTransfer.getData('index'));//ÊãñÂä®ÂÖÉÁ¥†ÂéüÂßã‰∏ãÊ†á
      let currentIndex = parseInt(index)//ÂΩìÂâç‰∏ãÊ†á
      // todo_listÈáåÁöÑÂÖÉÁ¥†
      if (type == this.list_type) {
        //Âà§Êñ≠‰ΩçÁΩÆ
        if (startIndex - currentIndex > 0) {//‰ΩçÁΩÆÂú®ÂêéÈù¢
          this[type].splice(currentIndex, 0, this[type][startIndex]);
          this[type].splice(startIndex + 1, 1)
        } else if (startIndex - currentIndex < 0) {//‰ΩçÁΩÆÂú®ÂâçÈù¢
          this[type].splice(currentIndex + 1, 0, this[type][startIndex]);
          this[type].splice(startIndex, 1)
        }
      }
      else if (!(type == this.list_type)) {
        //Âà§Êñ≠‰ΩçÁΩÆ
        this[type].splice(currentIndex, 0, this[this.list_type][startIndex]);
        this[this.list_type].splice(startIndex, 1)
      }
    },
    allowDrop (event) {//ÊãñÂä®ÁöÑÂÖÉÁ¥†Ê≠£Âú®Êüê‰∏ÄÂÖÉÁ¥†‰∏≠ÁßªÂä®
      event.preventDefault();
    },
    clear_todo (e) {//Ê∏ÖÈô§ÂÖ®ÈÉ®
      if (confirm('Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫Ê≠§Á±ªÔºü')) {
        this[e] = []
        localStorage[e] = JSON.stringify(this[e])
      }
    },
    rotateover () {
      this.rotate = 'rotateIn'
    },
    rotateout () {
      this.rotate = 'animate__rotateIn'
    }
  },
  mounted () {
    this.todo_list = localStorage.todo_list ? JSON.parse(localStorage.todo_list) : []
    this.done_list = localStorage.done_list ? JSON.parse(localStorage.done_list) : []
    this.timer = setInterval(() => {
      this.timing = new Date()
    });
  }
}
</script>

